<script>
// require styles
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  name: 'appCarrousel',
  components: {
    swiper,
    swiperSlide
  },
  props: {
    swiperVo: Object
  },
  data () {
    return {
      swiperOption: {
        // some swiper options/callbacks
        // 所有的参数同 swiper 官方 api 参数
        // ...
        speed: 600, // 滑动速度
        parallax: true, // 滑动偏移
        // loop: true, // 无限滚动
        spaceBetween: 30, // 间隔距离
        grabCursor: true, // 抓图光标
        autoplay: { // 自动轮播
          delay: 2000,
          disableOnInteraction: false
        },
        pagination: {
          el: '.swiper-pagination',
          dynamicBullets: true,
          // type: 'progressbar'|'fraction',
          clickable: true
        }
        // ,
        // scrollbar: {
        //   el: '.swiper-scrollbar',
        //   hide: true
        // }
        // ,
        // navigation: { // 左右切换按钮
        //   nextEl: '.swiper-button-next',
        //   prevEl: '.swiper-button-prev'
        // }
      }
    }
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.swiper
    }
  },
  mounted () {
    // current swiper instance
    // 然后你就可以使用当前上下文内的swiper对象去做你想做的事了
    // console.log('this is current swiper instance object', this.swiper)
    // this.swiper.slideTo(3, 1000, false)
  }
}
</script>

<style scoped>
#swiper {
  /* position: relative; */
  /* border: 1px dashed red; */
  border-radius: 7px;
  overflow: hidden;
  height: 100%;
}
.swiper-container {
  /* border: 1px dashed green; */
  height: 100%;
}
img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.swiper-slide {
  border: 1px dashed yellow;
  font-size: 18px;
  color: #000;
  /* -webkit-box-sizing: border-box; */
  box-sizing: border-box;
  padding: 40px 60px;
  /* background-color: transparent!important;
  justify-content: space-around!important; */
}
.parallax-bg {
  /* border: 1px dashed green; */
  position: absolute;
  /* left: 0;
  top: 0; */
  width: 130%;
  height: 100%;
  /* background: url('../../assets/images/computer.jpg') no-repeat top center scroll; */
  /* background-size: cover; */
}
.swiper-slide .title {
  font-size: 41px;
  font-weight: 300;
}
.swiper-slide .subtitle {
  font-size: 21px;
}
.swiper-slide .text {
  font-size: 14px;
  max-width: 400px;
  line-height: 1.3;
}
</style>



